import EventBus from "./EventBus";

class Model extends EventBus{
    constructor(options) {
        super();//EventBusconstructorconstructor
        ['data', 'update', 'create', 'delete', 'get'].forEach((key)=>{
            if (key in options){
                this[key]=options[key]
            }
        })
    }
    create() {
        console && console.error && console.error(' create')
    }

    delete() {
        console?.error?.(' delete')
    }

    update() {
        console && console.error && console.error(' update')
    }

    get() {
        console && console.error && console.error(' get')
    }
}
 export default Model